nav {
    position: fixed;
    overflow: hidden;
    opacity: 1;
    background-color:#080706;
    color: #EFEFEF;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    width: 100%;
    text-align: center;
    margin: 0;
    opacity: 0.9;
    z-index: 2;
    padding-top: 10px;
} 
button {
    margin: 0 auto;
    width: 50px;
    height: 50px;
    cursor: pointer;
}

#home-icon {
    margin: 0 auto;
    height: 50px;
    width: 50px;
}

nav a {
    text-decoration: none;
    color:  #EFEFEF;
    font-family: 'Raleway', sans-serif;
    font-weight: 100;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    font-size: 0.6em;
    height: 100%;
    padding-bottom: 2px;
}

.menu {
    width: 100;
    background-color: #594d46;
    opacity: 1;
    height: 50%;
}

.user-info {
    margin-left: 85%;
    margin-top: -30px;
    padding-bottom: 10px;
    font-family: 'Raleway', sans-serif;
}


@media screen and (max-width: 1200px) {
    .user-info {
        display: none;
    }
   
}



